* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100vh;
}

#root, #container {
  width: 100%;
  height: 100%;
  background-color: #7e7e7e;
}

#edt-div {
  width: 44.9%;
  height: 95%;
  position:absolute;
  top: 2.5%;
  left: 3%;
}

#edt-div-exp {
  width: 98.8%;
  height: 95%;
  position:absolute;
  top: 2.5%;
  left: 0.5%;
  z-index: 100;
}

#editor {
  outline: none;
  resize: none;
}

.label {
  width: 100%;
  height: 5%;
  background-color: black;
  color: white;
  text-align: center;
}

.exp-shr-div {
  position: absolute;
  top: 5px;
  right: 8px;
}

#pre-div {
  width: 45%;
  height: 95%;
  position:absolute;
  top: 2.5%;
  right: 3%;
  transition: all 0.1s linear;
}

#pre-div-exp {
  width: 99%;
  height: 95%;
  position:absolute;
  top: 2.5%;
  right: 0.5%;
  z-index: 100;
  transition: all 0.1s linear;
}

#preview {
  width: 100%;
  height:  95%;
  background-color: white;
  overflow: auto;
}